.conteiner {
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    margin-top: 24px;
}

.image {
    object-fit: cover;
    object-position: center;
    width: 100%;
    height: 100%;
}

.name {
    width: 100%;
    text-overflow: ellipsis;
    white-space: nowrap;
    overflow: hidden;
}

.firstimageoverlay {
    width: 64px;
    box-sizing: content-box;
    height: 64px;
    border: 2px solid transparent;
    border-radius: 50%;
    background: linear-gradient(55deg, rgba(128, 26, 178, 1) 1%, rgba(76, 76, 255, 1) 100%);
    position: absolute;
    z-index: 6;
}

