    left: 248px;
    border: 2px solid transparent;
    width: 64px;
    height: 64px;
    border-radius: 50%;
    background: #1C1C21;
    opacity: 0.6;
}

.imagebox {
    border-radius: 50%;
    width: 62px;
    height: 62px;
    display: flex;
    align-items: center;
    justify-content: center;
    object-fit: contain;
    border: 1px solid transparent;
    background-color: black;
}

.imagesbox {
    position: relative;
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    width: 436px;
    height: 68px;
}

.cost {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
}

.link {
    color: inherit;
    text-decoration: none;
}

.textgreen {
    color: hsla(180, 100%, 40%, 1)
}

.textwhite {
    color: rgba(242, 242, 243, 1)
