.ordersconteiner {
    width: 608px;
    display: flex;
    flex-direction: column;
    margin-top: 40px;
}

.feed {
    width: 100%;
    gap: 16px;
    height: calc(100vh - 208px);
    overflow-y: scroll;
    overflow-x: hidden;
    display: flex;
    flex-direction: column;
}

.element {
    width: 584px;
    height: 214px;
    display: flex;
    flex-direction: column;
    background: #1C1C21;
    box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.04), 0px 0px 2px rgba(0, 0, 0, 0.06), 0px 0px 1px rgba(0, 0, 0, 0.04);
    border-radius: 40px;
    padding: 24px;
    padding-top: 0;
    padding-bottom: 0;
    box-sizing: border-box;
}